        title: "Comunicazione",
        description:
          "Sono sempre disponibile ad effettuare una chiamata o una video conference per discutere del tuo prossimo prodotto. Metto a disposizione vari canali di comunicazione al fine di rendere il rapporto lavorativo funzionale e rapido.",
