          "Ogni progetto viene suddiviso in task a cui viene assegnato una priorità, al fine di poter sviluppare prima le funzionalità chiave, e successivamente le secondarie. <br/> Avere una buona organizzazione è fondamentale in particolar modo nella fase di analisi, in cui si effettuano le scelte progettuali che caratterizzeranno il prodotto. ",
